body, html {
  justify-content: center;
  align-items: center;
  height: 100%;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.hand {
  transform-origin: 50% 100%;
  position: absolute;
  bottom: 50%;
  left: 48%;
}

.hand--minutes {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate;
  transform: translate(-50%, -50%);
}

.hand--seconds {
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate;
  transform: translate(-50%, -50%);
}

.center-circle {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stopwatch .hand--minutes {
  animation-duration: 600s;
}

.stopwatch .hand--seconds {
  animation-duration: 10s;
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
